<template>
    <div class="forget">
        <div class="restContainer">
            <header>
                <a href="javascript:history.back(-1)">
                    <i class="el-icon-arrow-left"></i>
                </a>
                <span>重置密码</span>
            </header>

            <form class="restForm">
                <section class="input_container phone_number input_c">
                    <input type="text" placeholder="账号" name="username" v-model="username">
                </section>
                <section class="input_container input_c">
                    <input type="text" placeholder="旧密码" name="oldPassWord" v-model="oldpassword">
                </section>
                <section class="input_container input_c">
                    <input type="text" placeholder="请输入新密码" name="newPassWord" v-model="newpassword">
                </section>
                <section class="input_container input_c">
                    <input type="text" placeholder="请确认密码" name="confirmPassWord" v-model="confirmpassword">
                </section>
                <section class="input_container captcha_code_container input_c">
                    <input type="text" placeholder="验证码" v-model="capCode">
                    <div>
                        <img :src="capUrl" alt="">
                        <div @click="changeCap">
                            <p>看不清</p>
                            <p>换一张</p>
                        </div>
                    </div>
                </section>
            </form>
            <div class="alet_container  animated bounceIn" v-if="isShow1">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">请输入正确的账号</p>
                    <div class="confrim" @click="isShow1 = !isShow1">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow2">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">请输入旧密码</p>
                    <div class="confrim" @click="isShow2 = !isShow2">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow3">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">请输入新密码</p>
                    <div class="confrim" @click="isShow3 = !isShow3">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow4">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">请确认密码</p>
                    <div class="confrim" @click="isShow4 = !isShow4">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow5">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">请输入验证码</p>
                    <div class="confrim" @click="isShow5 = !isShow5">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow6">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">验证码错误</p>
                    <div class="confrim" @click="isShow6 = !isShow6">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow7">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">密码不正确</p>
                    <div class="confrim" @click="isShow7 = !isShow7">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow8">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">密码修改成功</p>
                    <div class="confrim" @click="isShow8 = !isShow8">确认</div>
                </section>
            </div>
            <div class="login_container" @click="loginContainer">确认修改</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Forget",
        data(){
            return {
                capUrl:"",
                // user:{},
                username:"",
                oldpassword:"",
                newpassword:"",
                confirmpassword:"",
                capCode: "",
                isShow1:false,
                isShow2:false,
                isShow3:false,
                isShow4:false,
                isShow5:false,
                isShow6:false,
                isShow7:false,
                isShow8:false,
            }
        },
        created(){
            this.requestCode();
            // this.user = JSON.parse(localStorage.getItem('profile'));
            // this.username = this.user.username
        },
        methods:{
            requestCode(){
                this.axios.post("/v1/captchas").then(res=>{
                    this.capUrl = res.code
                    // console.log(res);
                }).catch(err=>{});
            },
            changeCap(){
                //请求验证码
                this.requestCode();
            },
            //修改密码
            loginContainer(){
                this.axios.post("/v2/changepassword",{
                    username:this.username,
                    oldpassWord:this.oldpassword,
                    newpassword:this.newpassword,
                    confirmpassword:this.confirmpassword,
                    captcha_code:this.capCode
                }).then(res=>{
                    console.log(res);
                    if (res.message == "用户名参数错误" || this.username == ""){
                        this.isShow1 = true;
                    } else if (res.message == "必须添加旧密码" || this.oldpassword == ""){
                        this.isShow2 = true;
                    }else if (res.message == "必须填写新密码" || this.newpassword == ""){
                        this.isShow3 = true;
                    } else if (res.message == "必须填写确认密码" || this.confirmpassword == ""){
                        this.isShow4 = true
                    }else if (res.message == "请填写验证码" || this.captcha_code == ""){
                        this.isShow5 = true;
                    }else if (res.message == "验证码不正确"){
                        this.isShow6 = true;
                        this.requestCode();
                    }else if (res.message == "密码不正确"){
                        this.isShow7 = true;
                    }else {
                        this.isShow1 = false;
                        this.isShow2 = false;
                        this.isShow3 = false;
                        this.isShow4 = false;
                        this.isShow5 = false;
                        this.isShow6 = false;
                        this.isShow7 = false;
                        this.isShow8 = true;
                        this.username == "";
                        this.oldpassword == "";
                        this.newpassword == "";
                        this.confirmpassword == "";
                        this.capCode == "";
                    }
                }).catch(err=>{});
            },
        }
    }
</script>

<style scoped>
    .forget{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .restContainer{
        padding-top: 1.95rem;
    }
    .restForm{
        background-color: #fff;
        margin-top: .6rem;
    }
    .input_container{
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    .phone_number{
        padding: .3rem .8rem;
    }

    .input_c{
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    .input_c input{
        font-size: .7rem;
        color: #666;
        border: none;
        outline: none;
    }
    .input_c>div{
        display: flex;
        align-items: center;
    }
    .input_c>div p{
        font-size: .55rem;
        color: #666;
    }
    .input_c>div p+p {
        color: #3b95e9;
        margin-top: .2rem;
    }
    .input_c>div img{
        width: 3.5rem;
        height: 1.5rem;
        margin-right: .2rem;
    }
    .login_container{
        margin: 1rem .5rem;
        font-size: .7rem;
        color: #fff;
        background-color: #4cd964;
        padding: .5rem 0;
        border: 1px;
        border-radius: .15rem;
        text-align: center;
    }

    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon_s1{
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon_s2{
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>